:root {
  --main-light: #8dd3df;
  --main-dark: #152b2f;
  --main-accent: #548da3;
  --main-white: #ffffff;
  --main-yellow: #ffd419;

  --font-size-xl: 40px;
  --font-size-l: 20px;
  --font-size-m: 18px;
  --font-size-s: 15px;

  --shadow: rgba(59, 59, 75, 0.616);
}

html,
body {
  margin: 0;
  padding: 0;
  background-color: var(--white);
}

.Title {
  font-family: "Bungee", cursive;
  color: #152b2f;
  color: var(--main-dark);
  font-size: 40px;
  font-size: var(--font-size-xl);
  text-align: center;
  margin: 0px;
}

.Section {
  background-color: var(--white);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  margin-top: 50px;
}

.btn {
  font-family: "Raleway", sans-serif;
  border: 5px solid var(--main-accent);
  border-radius: 5px;
  background-color: var(--white);
  color: var(--main-accent);
  padding: 10px;
  font-size: var(--font-size-m);
  text-decoration: none;
  box-shadow: -1px 2px 4px var(--shadow);
  margin-right: 20px;
}

.btn:hover {
  transition: all 0.2s ease;
  background-color: var(--main-accent);
  color: var(--white);
}

.btn.empty {
  display: none;
}

.page-link {
  font-family: "Raleway", sans-serif;
  color: var(--main-accent);
  font-size: var(--font-size-l);
  border-bottom: 3px solid var(--main-accent);
  line-height: 1px;
  padding-bottom: 0px;
}

.WiC {
  padding: 50px;
  background-color: var(--main-light);
}

.WiC-Content {
  margin: 0 auto;
  display: grid;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  grid-template-columns: repeat(2, auto);
  align-items: center;
  grid-gap: 20px;
}

.WiC-Content img {
  max-width: 250px;
  height: auto;
  /* Shadow Effect */
  /* box-shadow: 0px 2px 10px var(--shadow);
  border-radius: 20px; */
}

.wic-title {
  font-family: "Bungee", cursive;
  color: var(--main-dark);
  font-size: 50px;
  text-align: left;
  line-height: 20px;
}

.wic-title.y {
  color: var(--white);
}

@media screen and (max-width: 996px) {
  .WiC {
    padding: 0px;
    background-color: var(--main-light);
    padding-bottom: 30px;
  }
  .WiC-Content img {
    /* display: none; */
    max-width: 200px;
  }

  .wic-title {
    font-size: 30px;
    line-height: 20px;
  }
}

@media screen and (max-width: 550px) {
  .WiC {
    padding: 20px;
    background-color: var(--main-light);
  }

  .WiC-Content img {
    display: none;
    /* max-width: 100px; */
  }

  .wic-title {
    font-size: 40px;
    line-height: 20px;
  }
}

/* Logo Font */
/* font-family: 'Bungee', cursive; */

/* Colours  */
:root {
  --main-light: #8dd3df;
  --main-dark: #152b2f;
  --main-off-light: #76c1ce;
  --white: #ffffff;
  --hover: #488994;
}

/* -------------------------------------- */

li,
a {
  text-decoration: none;
  list-style: none;
}

.Navbar {
  text-align: center;
  color: #ffffff;
  color: var(--white);
  background-color: #8dd3df;
  background-color: var(--main-light);
  display: flex;
  font-family: "Bungee", cursive;
  padding-left: 30px;
  padding-right: 30px;
  height: 70px;
  margin: 0;
  justify-content: left;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
}

/* WiC Logo --------------------------------- */

.navbar-logo {
  text-align: left;
  padding: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: grid;
  grid-template-columns: repeat(2, auto);
}

.navbar-logo img {
  width: 50px;
  height: auto;
  float: left;
  margin-right: 5px;
}

.navbar-logo img:hover,
.navbar-logo h1 {
  cursor: pointer;
}

.navbar-logo h1 {
  color: #152b2f;
  color: var(--main-dark);
  width: 170px;
  height: auto;
  font-size: 20px;
  line-height: 20px;
  margin: 5px;
}

/* SimpleNav */
.home-link {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
}

/* Menu Items --------------------------------- */
.Menu {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px;
  padding: 0px;
}

.menu-icon {
  margin: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: none;
}

.nav-menu {
  display: grid;
  justify-content: end;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  font-size: 15px;
}

.nav-links {
  color: #ffffff;
  color: var(--white);
  padding-left: 15px;
  padding-right: 15px;
}

.nav-links:hover {
  cursor: pointer;
  background-color: #488994;
  background-color: var(--hover);
  border-radius: 4px;
  transition: all 0.3s ease-out;
}

.fa-bars,
.fa-times {
  color: #ffffff;
  color: var(--white);
}

.nav-links-mobile {
  display: none;
}

@media screen and (max-width: 996px) {
  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
  }

  /* Nav Menu Icon------------------------------ */

  .menu-icon {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-70%, 70%);
            transform: translate(-70%, 70%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-70%, 50%);
            transform: translate(-70%, 50%);
    font-size: 2.2rem;
  }

  /* Nav Menu---------------------------------- */
  .Navbar {
    padding: 0;
    background-color: #8dd3df;
    background-color: var(--main-light);
  }

  .Menu {
    width: 100%;
    padding: 0;
  }

  .nav-menu {
    padding: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 35px;
    justify-content: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-template-columns: repeat(1, auto);
    width: 100%;
    background-color: #76c1ce;
    background-color: var(--main-off-light);
    position: absolute;
    left: -200%;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background-color: #76c1ce;
    background-color: var(--main-off-light);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    width: 100%;
    flex: 1 1;
  }

  .nav-links:hover {
    background: #488994;
    background: var(--hover);
    border-radius: 0;
  }
}

@media screen and (max-width: 400px) {
  .nav-links {
    font-size: var(--font-size-xl);
  }
}

.about-details {
  font-family: "Raleway", sans-serif;
  font-size: var(--font-size-m);
  line-height: 25px;
  text-align: justify;
}

.AboutUs {
  width: 70%;
  max-width: 1000px;
  margin: 0 auto;
}

@media screen and (max-width: 500px) {
  .about-details {
    font-size: var(--font-size-s);
    line-height: 20px;
    text-align: justify;
  }
}

.Event {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  text-align: center;
  padding: 15px;
}

.event-info-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 20px;
  max-width: 800px;
  margin: 0;
  padding-bottom: 20px;
}

.event-poster {
  height: auto;
  border-radius: 10px;
  box-shadow: 0px 2px 10px var(--shadow);
}

.event-details {
  color: var(--main-dark);
  font-family: "Raleway", sans-serif;
  text-align: left;
  width: 100%;
}

.event-details h2 {
  color: var(--main-dark);
  font-family: "Bungee", cursive;
  text-align: left;
  margin: 0;
  line-height: 30px;
}

.event-details h3 {
  color: var(--main-accent);
  margin: 0;
  margin-bottom: 10px;
}

.event-details-description {
  text-align: justify;
  color: var(--main-dark);
  font-family: "Raleway", sans-serif;
  font-size: var(--font-size-m);
}

.event-details-register {
  margin-top: 50px;
  margin-bottom: 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.btn-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  /* margin-bottom: 50px; */
}

@media screen and (max-width: 996px) {
  .event-info-container {
    margin: 20px;
  }
  .event-details-register {
    display: grid;
    grid-template-rows: 1fr;
  }
  .btn.register {
    margin: 0;
    margin-bottom: 10px;
    text-align: center;
  }
}

@media screen and (max-width: 450px) {
  .event-info-container {
    grid-template-columns: repeat(1, auto);
  }

  .css-text-901oao {
    text-align: left;
    font-size: var(--font-size-s);
  }
  .event-details-register {
    margin: 0 auto;
  }
}

.join-container {
  display: grid;
  grid-template-columns: 20% 70%;
  align-items: center;
  justify-items: center;
  text-align: center;
  max-width: 700px;
  width: 80%;
  padding: 10px;
  margin: 0 auto;
  margin-bottom: 30px;
  font-size: var(--font-size-m);
  /* background-color: brown; */
}

.join-info p {
  font-family: "Raleway", sans-serif;
  text-align: left;
}
@media screen and (max-width: 600px) {
  .join-container {
    grid-gap: 20px;
  }
}

.Footer {
  text-align: left;
  padding: 20px;
  margin-top: 50px;
  background-color: var(--main-light);
}

.social-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.socials {
  font-family: "Raleway", sans-serif;
  font-size: var(--font-size-m);
  line-height: 30px;
  color: var(--white);
}

.info-container {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-gap: 10px;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.info-container img {
  width: 30px;
  height: auto;
  padding: 5px;
}

.info-container img:hover {
  cursor: pointer;
  background-color: var(--main-accent);
  border-radius: 8px;
}

.info-container h2 {
  font-family: "Raleway", sans-serif;
  color: var(--white);
  font-size: var(--font-size-m);
}

